  src: url("/src/fonts/TTLakes-Bold.woff2") format("woff2"), url("/src/fonts/TTLakes-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal; }

* {
  font-family: "TT Lakes", sans-serif;
  box-sizing: border-box; }

body {
  margin: 0;
  background-color: #F8F8F8;
  color: #000000; }

.btn {
  border: none;
  background: none; }

.btn :hover {
  cursor: pointer; }
